import React, { Component } from "react";
import logo from "../../../src/images/svg/logo.svg";
import NetflixN from "../../../src/images/svg/netflixN.png";
// import {  } from "react-router-dom";
import styled from "styled-components";
import "../../css/App.css";
import MoviePage from "../../pages/Movie";
import Footer2 from "../moviePage/Footer";
import TabComponent from "./TabComponent";
import {
	BrowserRouter as Router,
	Switch,
	Route,
	Link,
	NavLink,
} from "react-router-dom";

class Header extends Component {
	constructor(props) {
		super(props);
		this.state = {
			serviceCode: "Service Code",
		};
		this.serviceClick = this.serviceClick.bind(this);
	}

	serviceClick = () => {
		this.setState({ serviceCode: "cTD2eZxfHqwxhMLX" });
	};

	render() {
		return (
			<div className="intro-page">
				<HeaderComponent className="header-container_intro">
					<div className="overlay"></div>
					<div className="header-top">
						<Logo src={logo} />
						<Link to="/signin">
							<button type="button" className="signIn-btn">
								Sign In
							</button>
							{/* Sign In */}
						</Link>
					</div>
					{/* Header Content */}
					<StoryCard>
						<HeroCard>
							<Title>Unlimited movies, TV shows, and more.</Title>
							<h2>Watch anywhere. Cancel at any time.</h2>
						</HeroCard>
						<EmailForm>
							<StoryCardMssage>
								Ready to watch? Enter your email to create or restart your
								membership.
							</StoryCardMssage>
							<EmailLockUp>
								<SimpleForm>
									<EmailInput placeholder="Email address" type="text" />
								</SimpleForm>
								<Link to="/signin">
									<Button>TRY 30 DAYS FREE</Button>
								</Link>
								<p>Email is required.</p>
							</EmailLockUp>
						</EmailForm>
						{/* </div> */}
					</StoryCard>
				</HeaderComponent>
				<Tab>
					<TabComponent className="tabcomponent" />
					<div className="email-require">
						<StoryCard>
							<StoryCardMssage>
								Ready to watch? Enter your email to create or restart your
								membership.
							</StoryCardMssage>
							<EmailForm>
								<EmailLockUp>
									<SimpleForm>
										<EmailInput placeholder="Email address" type="text" />
									</SimpleForm>
									<Link to="/signin">
										<Button>TRY 30 DAYS FREE</Button>
									</Link>
									<p>Email is required.</p>
								</EmailLockUp>
							</EmailForm>
						</StoryCard>
					</div>
					<Footer2
						serviceClick={this.serviceClick}
						serviceCode={this.state.serviceCode}
						color="black"
					/>
				</Tab>
			</div>
		);
	}
}

export default Header;

const StoryCardMssage = styled.p`
	max-width: none;
	padding-bottom: 7px;
	font-size: 1.2rem;
`;

const Tab = styled.div`
	.email-require {
		@media only screen and (max-width: 1100px) {
			display: none;
		}
	}
	.tabcomponent {
		background-color: black;
		@media only screen and (max-width: 1100px) {
			display: none;
		}
	}
	width: 100%;
	height: 15vh;
	background-color: black;
	position: relative;
	border-bottom: solid 7px #222;
`;

const Footer = styled.div`
	position: relative;
	width: 100%;
	height: 20%;
	margin-top: 200px 174px;
	/* background-color: red; */
`;

const EmailLockUp = styled.div`
	justify-content: center;
	align-items: center;
	margin-left: 50px;
	p {
		text-align: left;
		margin: 0;
		padding: 10px;
		font-size: 15px;
		color: #ffa00a;
	}
	/* text-align: left;
	margin: 0; */
`;

const SimpleForm = styled.div``;

const EmailInput = styled.input`
	/* margin-bottom: 10px; */
	/* display: inline-flex; */
	/* padding: 0;
*/
	/* display: flex; */
	color: #8c8c8c;
	border-radius: 4px;
	font-size: 16px;
	margin: 0;
	float: left;
	position: relative;
	padding: 0 1em;
	width: 60%;
	background: #fff;
	max-width: 450px;
	height: 62px;
	margin-top: -2px;
	min-height: 60px;
`;

const Button = styled.button`
	/* float: left; */
	border-radius: 2px;
	align-items: center;
	justify-content: center;
	/* padding: 0 0; */
	width: 30vw;
	max-width: 380px;
	position: relative;
	text-align: center;
	color: #fff;
	border: none;
	display: flex;
	background: #f40612;
	font-size: calc(10px + 1vw);
	/* font-size: 1.625rem; */
	min-height: 60px;
`;

const StoryCard = styled.div`
	position: relative;
	width: 100%;
	padding: 75px 0;
	padding-bottom: 180px;
	max-width: 950px;
	margin: 0 auto;
	/* background-color: black; */
	text-align: center;
	z-index: 1;
`;

const HeroCard = styled.div`
	max-width: 640px;
	margin: 0 auto;

	h2 {
		font-size: calc(12px + 1.5vw);
		font-weight: 1;
		margin: 1rem auto;
	}
`;

const Logo = styled.img`
	/* width: 134px;
	height: 44px; */
	position: relative;
	height: calc(40px);
	top: 10px;
	/* top: calc(50%); */
	left: 5%;

	/* transform: translate(-50%, -50%); */
`;

// Header Container
const HeaderComponent = styled.div`
	.signIn-btn {
		/* position: absolute; */
		/* display: flex; */
		position: relative;
		float: right;
		right: 0;
		border: none;
		/* margin: 1.125rem 3% 0; */
		margin: 20px 3%;
		height: 2rem;
		padding: 0.4275rem 1.0625rem;
		font-weight: 550;
		line-height: normal;
		border-radius: 0.1875rem;
		/* font-size: 1rem; */
		background: var(--main-red);
		translate: transform(-50%, -50%);
		cursor: pointer;
		color: white;
		transition: background 0.2s ease-in;
		&:hover {
			background: var(--main-red-hover);
		}
	}

	.header-top {
		position: relative;
		height: 7vw;
		z-index: 1;
	}

	// Header Content
	.header-content {
		max-width: 550px;
		position: relative;
		margin: 4.5rem auto 0;
		display: flex;
		justify-content: center;
		align-content: center;
		text-align: center;
		flex-direction: column;
		z-index: 1;
	}
`;

// Main Title
const Title = styled.h1`
	max-width: 640px;
	margin: 0 auto;
	font-size: calc(12px + 3vw);
	line-height: 1em;
	/* font-weight: 2vw; */
	/* font-size: 3.125rem; */
	/* @media only screen and (max-width: 1449px) and (min-width: 950px) {
		font-size: 3.125rem;
	} */
`;

// Subtitle
const SubTitle = styled.h2`
	/* font-weight: 400; */
	font-size: 1.625rem;
	line-height: 1.25em;
	margin: 0 0 1.875rem;
	/* text-transform: uppercase; */
`;

const EmailForm = styled.ul`
	h3 {
		padding: 0 5%;
		font-weight: 50;
		@media only screen and (max-width: 1449px) and (min-width: 950px),
			only screen and (min-width: 1450px) {
			max-width: none;
			padding-bottom: 20px;
			font-size: 1.2rem;
		}
	}
`;
